import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

import { AppComponent } from './app.component';
import { RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { PaymentsComponent } from './payments/payments.component';
import { AssignmentComponent } from './assignment/assignment.component';
import { LibraryComponent } from './library/library.component';
import { ForumComponent } from './forum/forum.component';
import { SchcalenderComponent } from './schcalender/schcalender.component';
import { GeneralService } from './general.service';
import { NotifyService } from './notify.service';
import { FullCalendarModule } from 'ng-fullcalendar';
import { ForumhomeComponent } from './forum/forumhome/forumhome.component';

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    PaymentsComponent,
    AssignmentComponent,
    LibraryComponent,
    ForumComponent,
    SchcalenderComponent,
    ForumhomeComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FullCalendarModule,
    RouterModule.forRoot([
      {
        path: '',
        redirectTo: 'dashboard',
        pathMatch: 'full'

      },
      {
        path: 'dashboard',
        component: DashboardComponent,

      },
      {
        path: 'assignment-manager',
        component: AssignmentComponent,

      },
      {
        path: 'library',
        component: LibraryComponent,

      },
      {
        path: 'discussions',
        component: ForumhomeComponent,

      },
      {
        path: 'discussion/:thread_id',
        component: ForumComponent,

      },
      {
        path: 'payments',
        component: PaymentsComponent,

      }
      ])
  ],
  providers: [{provide: LocationStrategy, useClass: HashLocationStrategy}, GeneralService, NotifyService],
  bootstrap: [AppComponent]
})
export class AppModule { }



