import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit  {
  clock: string;
  dates: string;
  schoolid: string;
  studentname: string;
  studentid: string;
  szclass: string;
  constructor() {}
  ngOnInit() {
    sessionStorage.setItem('studentid', 'GSIS09882');
    sessionStorage.setItem('studentname', 'Chinedu Ekene Okpala');
    sessionStorage.setItem('szclass', 'JSS 2');
    sessionStorage.setItem('schoolid', 'GSISCHOOL');
    if (!sessionStorage.getItem('studentid') && !sessionStorage.getItem('studentname') && !sessionStorage.getItem('szclass') && !sessionStorage.getItem('schoolid')) {
      document.location.href = '../studentportal/v1.2/';
    } else {
      // process to getting started page...
    }
    this.time();
    setInterval(() => {
      this.time();
      this.studentname = sessionStorage.getItem('studentname');
    }, 500);
  }

  logout(): void {
    sessionStorage.clear();
    document.location.href = '../studentportal/v1.2/';
  }
  time(): void {
    const monthNames = ['January', 'February', 'March', 'April', 'May', 'June',
      'July', 'August', 'September', 'October', 'November', 'December'];
    const now = new Date();
    const dd: any = now.getDate();
    const mm: any = monthNames[now.getMonth()];
    const yy: any = now.getFullYear();
    let hr: any = now.getHours();
    const ampm = hr >= 12 ? 'PM' : 'AM';
    hr = hr % 12;
    hr = hr ? hr : 12;
    if (hr === 0) {
      hr = 12;
    } else if (hr.toString().length === 1) {
      hr = '0' + hr;
    }
    let mins: any = now.getMinutes();
    if (mins.toString().length === 1) {
      mins = '0' + mins;
    }
    let ss: any = now.getSeconds();
    if (ss.toString().length === 1) {
      ss = '0' + ss;
    }

    this.dates = mm + ' ' + dd + ', ' + yy;
    this.clock = hr + ':' + mins + ':' + ss + ' ' + ampm;
  }

}



