import { Component, OnInit } from '@angular/core';
import { GeneralService } from '../../general.service';
import { ForumData } from './forumdata';
import { NotifyService } from '../../notify.service';

@Component({
  selector: 'app-forumhome',
  templateUrl: './forumhome.component.html',
  styleUrls: ['./forumhome.component.css']
})
export class ForumhomeComponent implements OnInit {

  constructor(private _genservice: GeneralService, private _notify: NotifyService) { }
  forumdata: ForumData[];
  subject: string;
  post_topic: string;
  post_title: string;
  post_desc: string;
  ngOnInit() {
    this.subject = null;
    this.post_topic = null;
    this.post_title = null;
    this.post_desc = null;
    $(function() {
      $('textarea').froalaEditor();
      $('#PreviewText').froalaEditor('events.trigger', 'blur');
    });
    this._genservice.GetThreadsData().subscribe(response => {
      this.forumdata = response;
    });
  }
  public formControl(action) {
    if(action == 'show') {
      $('.sort-discussion-form').fadeIn();
    } else
      if (action == 'hide') {
      $('.sort-discussion-form').fadeOut();
    }
  }
  submitQuestion() {
    this.post_desc = $('#PreviewText').val();
    if (this.subject == null ||  this.subject == '' || this.subject == "" || this.subject  == 'undefined') {
      this._notify.showNotification('top', 'left', 'No Thread Subject Selected', 'danger');
      return;
    }
    if (this.post_topic == null ||  this.post_topic == '' || this.post_topic == "" || this.post_topic  == 'undefined') {
      this._notify.showNotification('top', 'left', 'No Thread Topic Selected', 'danger');
      return;
    }
    if (this.post_title == null ||  this.post_title == '' || this.post_title == "" || this.post_title  == 'undefined') {
      this._notify.showNotification('top', 'left', 'Please Enter a Thread Title', 'danger');
      return;
    }
    if (this.post_desc == null ||  this.post_desc == '' || this.post_desc == "" || this.post_desc  == 'undefined') {
      this._notify.showNotification('top', 'left', 'Please Enter a Thread Description', 'danger');
      return;
    }
    const monthNames = ['January', 'February', 'March', 'April', 'May', 'June',
      'July', 'August', 'September', 'October', 'November', 'December'];
    const now = new Date();
    const dd: any = now.getDate();
    const mm: any = monthNames[now.getMonth()];
    const yy: any = now.getFullYear();
    let hr: any = now.getHours();
    const ampm = hr >= 12 ? 'PM' : 'AM';
    hr = hr % 12;
    hr = hr ? hr : 12;
    if (hr === 0) {
      hr = 12;
    } else if (hr.toString().length === 1) {
      hr = '0' + hr;
    }
    let mins: any = now.getMinutes();
    if (mins.toString().length === 1) {
      mins = '0' + mins;
    }
    const post_date_time = mm + ' ' + dd + ', ' + yy + ', at ' + hr + ':' + mins + ' ' + ampm;
    this._genservice.CreateThread(this.subject, this.post_topic, this.post_title, this.post_desc, post_date_time).
    subscribe(response => {
      console.log(response);
    });
  }

}



