import { Component, OnInit } from '@angular/core';
import { GeneralService } from '../general.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  notice1 = [];
  notice2 = [];
  notice3 = [];
  mge_type: string;
  mge_from: string;
  post_date: string;
  file_url: string;
  full_message: string;

  constructor(private _genservice: GeneralService) { }

  ngOnInit() {
    $(function() {
      $('.bxslider').bxSlider({
        mode: 'horizontal',
        captions: true,
        slideWidth: 600
      });
    });
    this._genservice.getGeneralNotifications().subscribe(response => {
      for (let v = 0; v < response.length; v++) {

        // const date = response[v].szpost_date;
        // const dateArr = date.split('-');
        // const newDateFormat = dateArr[2] + '-' + dateArr[1] + '-' + dateArr[0];
        const filename = encodeURI(response[v].sz_filename);

        let messageArr = response[v].szmessage.split(' ');
        let mgePart;
        if (messageArr.length > 15) {
          messageArr = messageArr.slice(0, 15);
           mgePart = messageArr.join(' ')  + ' ...<a href="#/dashboard">more</a>';
        } else {
          mgePart = response[v].szmessage;
        }

        if (response[v].sz_filename === '') {
          this.notice1.push({
            id: response[v].id,
            szfrom: response[v].szfrom,
            sztype: response[v].sztype,
            szpost_date: response[v].szpost_date,
            mgePart: mgePart,
            szmessage: response[v].szmessage,
            filename: 'No File',
            sz_filename: 'No File'
          });
        } else {
          this.notice1.push({
            id: response[v].id,
            szfrom: response[v].szfrom,
            sztype: response[v].sztype,
            szpost_date: response[v].szpost_date,
            mgePart: mgePart,
            szmessage: response[v].szmessage,
            filename: filename,
            sz_filename: '<a target="_blank" href=http://localhost/iKolilu/studentportal/v1.2/assets/' + filename + '><i class="fa fa-2x fa-paperclip"> </i></a>'
          });
        }
      }
    });
    this._genservice.getClassNotifications().subscribe(response => {
      for (let v = 0; v < response.length; v++) {

        // const date = response[v].szpost_date;
        // const dateArr = date.split('-');
        // const newDateFormat = dateArr[2] + '-' + dateArr[1] + '-' + dateArr[0];

        let messageArr = response[v].szmessage.split(' ');
        let mgePart;
        if (messageArr.length > 15) {
          messageArr = messageArr.slice(0, 15);
          mgePart = messageArr.join(' ')  + ' ...<a href="#/dashboard">more</a>';
        } else {
          mgePart = response[v].szmessage;
        }

        if (response[v].sz_filename === '') {
          this.notice2.push({
            id: response[v].id,
            szfrom: response[v].szfrom,
            sztype: response[v].sztype,
            szpost_date: response[v].szpost_date,
            mgePart: mgePart,
            szmessage: response[v].szmessage,
            filename: 'No File',
            sz_filename: 'No File'
          });
        } else {
          const filename = encodeURI(response[v].sz_filename);
          this.notice2.push({
            id: response[v].id,
            szfrom: response[v].szfrom,
            sztype: response[v].sztype,
            szpost_date: response[v].szpost_date,
            mgePart: mgePart,
            szmessage: response[v].szmessage,
            filename: filename,
            sz_filename: '<a target="_blank" href=http://localhost/iKolilu/studentportal/v1.2/assets/' + filename + '><i class="fa fa-2x fa-paperclip"> </i></a>'
          });
        }
      }
    });
    this._genservice.getDirectNotifications().subscribe(response => {
      for (let v = 0; v < response.length; v++) {

        // const date = response[v].szpost_date;
        // const dateArr = date.split('-');
        // const newDateFormat = dateArr[2] + '-' + dateArr[1] + '-' + dateArr[0];

        let messageArr = response[v].szmessage.split(' ');
        let mgePart;
        if (messageArr.length > 15) {
          messageArr = messageArr.slice(0, 15);
          mgePart = messageArr.join(' ')  + ' ...<a href="#/dashboard">more</a>';
        } else {
          mgePart = response[v].szmessage;
        }

        if (response[v].sz_filename === '') {
          this.notice3.push({
            id: response[v].id,
            szfrom: response[v].szfrom,
            sztype: response[v].sztype,
            szpost_date: response[v].szpost_date,
            mgePart: mgePart,
            szmessage: response[v].szmessage,
            filename: 'No File',
            sz_filename: 'No File'
          });
        } else {
          const filename = encodeURI(response[v].sz_filename);
          this.notice3.push({
            id: response[v].id,
            szfrom: response[v].szfrom,
            sztype: response[v].sztype,
            szpost_date: response[v].szpost_date,
            mgePart: mgePart,
            szmessage: response[v].szmessage,
            filename: filename,
            sz_filename: '<a target="_blank" href=http://localhost/iKolilu/studentportal/v1.2/assets/' + filename + '><i class="fa  fa-2x fa-paperclip"> </i></a>'
          });
        }
      }
    });
  }
  getNoticeData(id, data): void {
    for (let i = 0;  i < data.length; i++) {
      if (data[i].id === id) {
        this.mge_type = data[i].sztype;
        this.post_date = data[i].szpost_date;
        this.mge_from = data[i].szfrom;
        this.file_url = data[i].sz_filename;
        this.full_message = data[i].szmessage;
      }
    }
  }
}



