import { Component, OnInit } from '@angular/core';
import { GeneralService } from '../general.service';
import { ClassSubject } from '../classsubject';
import { NotifyService } from '../notify.service';
import { SearchLib } from '../searchlib';

@Component({
  selector: 'app-library',
  templateUrl: './library.component.html',
  styleUrls: ['./library.component.css']
})
export class LibraryComponent implements OnInit {
  subjects: ClassSubject[];
  searches: SearchLib[];
  category: string;
  key: string;
  constructor(private _genservice: GeneralService, private _notify: NotifyService ) { }

  ngOnInit() {
    this._genservice.getClassSubjects().subscribe(response => { this.subjects = response; });
  }
  Onsearch(): void {
    $('.search-items').fadeOut('fast');
    if (this.key == null ||  this.key == '' || this.key == "" || this.key  == 'undefined') {
      this._notify.showNotification('top', 'right', 'Enter a Search Key!', 'danger');
      return;
    }
    if (this.category == null ||  this.category == '' || this.category == "" || this.category  == 'undefined') {
      this._notify.showNotification('top', 'right', 'Select a Category!', 'danger');
      return;
    }
    this._genservice.SearchLib( this.key.toUpperCase(), this.category.toUpperCase()).subscribe(response => {
      this.searches = response;
      // console.log(this.searches['0']['success']);
      if ( this.searches['0']['success'] === true ) {
        $('.search-items').html(this.searches['0']['items']).fadeIn('fast');
      } else {
        $('.search-items').html('Sorry, there are no matching result for <b>' + this.searches['0']['key'] + '</b> in <b>' + this.searches['0']['category'] + '</b>').fadeIn('fast');
      }
    });
  }
}



