import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import { SchoolCalendar } from './calendar';
import { StudentDetails } from './studentdetails';
import { BillsPayments } from './billpayments';
import { StudentClass } from './studentclasses';
import { BillHistory } from './billhistory';
import { BillsSummary } from './billsummary';
import { ForumData } from './forum/forumhome/forumdata';
import { Notices } from './notices';
import { ClassSubject } from './classsubject';
import { SearchLib } from './searchlib';

@Injectable()
export class GeneralService {
  public apiPath = 'https://www.ikolilu.com/api/v1.0/portal.php/';
  // public localapiPath = 'http://localhost/iKolilu/studentportal/v1.2/api/studentportalapi_.php/';
  public localapiPath = 'https://www.ikolilu.com/studentportal/v1.2/api/studentportalapi_.php/';
  constructor(private http: HttpClient) {}

  public getStudentDetails(): Observable<StudentDetails> {
    const localPath = this.apiPath + 'GetStudentDetails/';
    const studentid  = sessionStorage.getItem('studentid');
    let params = new HttpParams();
    params = params.append('student_id', studentid);
    return this.http.get<StudentDetails>(localPath, { params });
  }
  public getCalendar(): Observable<SchoolCalendar[]> {
    const localPath = this.apiPath + 'GetSchoolCalendar/';
    const schoolid  = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('szschoolid', schoolid);
    return this.http.get<SchoolCalendar[]>(localPath, { params });
  }
  public getStudentClasses(): Observable<StudentClass> {
    const localPath = this.apiPath + 'GetStudentClass/"';
    const studentid = sessionStorage.getItem('studentid');
    let params = new HttpParams();
    params = params.append('sz_wardid', studentid);
    return this.http.get<StudentClass>(localPath, { params });
  }
  public getStudentBillsPayments(szterm: string, szclass: string): Observable<BillsPayments> {
    const localPath = this.apiPath + 'GetMyWardBillsAndPayments/';
    const studentid = sessionStorage.getItem('studentid');
    const schoolid  = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('sz_wardid', studentid);
    params = params.append('szschoolid', schoolid);
    params = params.append('szclassid', szclass);
    params = params.append('sz_term', szterm);
    return this.http.get<BillsPayments>(localPath, { params });
  }
  public getStudentBillsPaymentsHistory(): Observable<BillHistory[]> {
    const localPath = this.apiPath + 'GetMyWardBillsAndPaymentHistory/';
    const studentid = sessionStorage.getItem('studentid');
    const schoolid  = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('sz_wardid', studentid);
    params = params.append('szschoolid', schoolid);
    return this.http.get<BillHistory[]>(localPath, { params });
  }
  public getStudentSummary(szterm: string, szclass: string): Observable<BillsSummary[]> {
    const localPath = this.apiPath + 'GetWardSummary/';
    const studentid = sessionStorage.getItem('studentid');
    const schoolid  = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('sz_wardid', studentid);
    params = params.append('szschoolid', schoolid);
    params = params.append('szclassid', szclass);
    params = params.append('sz_term', szterm);
    return this.http.get<BillsSummary[]>(localPath, { params });
  }
  public getGeneralNotifications(): Observable<Notices[]> {
    const localPath = this.localapiPath + 'GeneralNotifications/';
    const szschoolid = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('szschoolid', szschoolid);
    return this.http.get<Notices[]>(localPath, { params });
  }
  public getClassNotifications(): Observable<Notices[]> {
    const localPath = this.localapiPath + 'ClassNotifications/';
    const szclass = sessionStorage.getItem('szclass');
    const szschoolid = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('szclass', szclass);
    params = params.append('szschoolid', szschoolid);
    return this.http.get<Notices[]>(localPath, { params });
  }
  public getDirectNotifications(): Observable<Notices[]> {
    const localPath = this.localapiPath + 'DirectNotifications/';
    const studentid = sessionStorage.getItem('studentid');
    const szclass = sessionStorage.getItem('szclass');
    const szschoolid = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('studentid', studentid);
    params = params.append('szclass', szclass);
    params = params.append('szschoolid', szschoolid);
    return this.http.get<Notices[]>(localPath, { params });
  }
  public GetThreadsData(): Observable<ForumData[]> {
    // const localPath = this.localapiPath + 'GetThreadsData/';
    // const szclass = sessionStorage.getItem('szclass');
    // const schoolid = sessionStorage.getItem('schoolid');
    // let params = new HttpParams();
    // params = params.append('szclass', szclass);
    // params = params.append('schoolid', schoolid);
    // return this.http.get<ForumData[]>(localPath, { params });
    const localPath = 'http://localhost/ikolilu/studentportal/v1.2/assets/forumdata.json';
    return this.http.get<ForumData[]>(localPath);
  }
  public GetThreadData(thread_id): Observable<ForumData[]> {
    // const localPath = this.localapiPath + 'GetThreadData/';
    // const szclass = sessionStorage.getItem('szclass');
    // const schoolid = sessionStorage.getItem('schoolid');
    // let params = new HttpParams();
    // params = params.append('thread_id', thread_id);
    // params = params.append('szclass', szclass);
    // params = params.append('schoolid', schoolid);
    // return this.http.get<ForumData[]>(localPath, { params });
    const localPath = 'http://localhost/ikolilu/studentportal/v1.2/assets/forumdata.json';
    return this.http.get<ForumData[]>(localPath);
  }
  public getClassSubjects(): Observable<ClassSubject[]>  {
    const localPath = this.localapiPath + 'ClassSubjects/';
    const sz_studentid = sessionStorage.getItem('studentid');
    const sz_class = sessionStorage.getItem('szclass');
    const sz_schoolid = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('sz_studentid', sz_studentid);
    params = params.append('sz_class', sz_class);
    params = params.append('sz_schoolid', sz_schoolid);
    return this.http.get<ClassSubject[]>(localPath, { params });
  }
  public SearchLib(key, category): Observable<SearchLib[]>  {
    const localPath = this.localapiPath + 'SearchLibraryBooks/';
    const sz_class = sessionStorage.getItem('szclass');
    const sz_schoolid = sessionStorage.getItem('schoolid');
    let params = new HttpParams();
    params = params.append('search_key', key);
    params = params.append('search_cat', category);
    params = params.append('sz_class', sz_class);
    params = params.append('sz_schoolid', sz_schoolid);
    return this.http.get<SearchLib[]>(localPath, { params });
  }
  public CreateThread(subject, post_topic, post_title, post_text, post_date_time) {
    const localPath = this.localapiPath + 'CreateThread/';
    const studentid = sessionStorage.getItem('studentid');
    const studentname = sessionStorage.getItem('studentname');
    const szclass = sessionStorage.getItem('szclass');
    const schoolid = sessionStorage.getItem('schoolid');
    const user_signature = 'Life is short. There are more things we still do not know.';
    return this.http.post(localPath, {
      subject: subject, post_topic: post_topic, post_title: post_title, post_text: post_text,
      orginal_poster: studentname, orginal_posterid: studentid, post_date_time: post_date_time,
      user_signature: user_signature, szclass: szclass, schoolid: schoolid
    });
  }
  public QuickReplyThread(
        thread_id, sn, subject, topic, post_title, post_text, orginal_poster, orginal_posterid,
        post_date_time, user_signature, views, answers, szclass, schoolid ) {
        const localPath = this.localapiPath + 'QuickReplyThread/';
        return this.http.post(localPath, {
          thread_id: thread_id, sn: sn, subject: subject, topic: topic, post_title: post_title, post_text: post_text,
          orginal_poster: orginal_poster, orginal_posterid: orginal_posterid, post_date_time: post_date_time,
          user_signature: user_signature, views: views, answers: answers, szclass: szclass, schoolid: schoolid
        });
  }
  public VoteUpdate(thread_id, countID, newvote) {
    const localPath = this.localapiPath + 'VoteUpdate/';
    const studentid = sessionStorage.getItem('studentid');
    const schoolid = sessionStorage.getItem('schoolid');
    return this.http.post(localPath, { thread_id: thread_id, sn: countID, vote: newvote });
  }
  public getIpAddress() {
    const localPath = 'http://freegeoip.net/json/?callback';
    return this.http.get(localPath);
  }
  public Views(ip, thread_id) {
    const localPath = this.localapiPath + 'Views/';
    const studentid = sessionStorage.getItem('studentid');
    const szclass = sessionStorage.getItem('szclass');
    const schoolid = sessionStorage.getItem('schoolid');
    return this.http.post(localPath, {ip: ip, thread_id: thread_id, studentid: studentid, szclass: szclass, schoolid: schoolid});
  }

}



