import { Component, OnInit, ViewChild } from '@angular/core';
import { CalendarComponent } from 'ng-fullcalendar';
import { Options } from 'fullcalendar';

import { GeneralService } from '../general.service';
import {SchoolCalendar} from '../calendar';

@Component({
  selector: 'app-schcalender',
  templateUrl: './schcalender.component.html',
  styleUrls: ['./schcalender.component.css']
})

export class SchcalenderComponent implements OnInit {

  displayEvent: any;
  calendarOptions: Options;
  calendar = [];
  @ViewChild(CalendarComponent) ucCalendar: CalendarComponent;
  constructor(private _genservice: GeneralService) { }

  ngOnInit() {
    this._genservice.getCalendar().subscribe( data => {
      for (let v = 0; v < data.length; v++) {
          this.calendar.push({
            id: data[v].id,
            title: data[v].title,
            start: data[v].start,
            end: data[v].end,
            textColor: '#ffffff',
            backgroundColor: '#3a87ad'
          });
      }
      this.calendarOptions = {
        editable: true,
        eventLimit: false,
        header: {
          left: 'prev,next today',
          center: 'title',
          right: 'month,agendaWeek,agendaDay,listMonth'
        },
        events: this.calendar
      };
    });
  }

  clickButton(model: any) {
    this.displayEvent = model;
  }
  eventClick(model: any) {
    model = {
      event: {
        id: model.event.id,
        start: model.event.start,
        end: model.event.end,
        title: model.event.title,
        allDay: model.event.allDay
        // other params
      },
      duration: {}
    };
    this.displayEvent = model;
  }
  updateEvent(model: any) {
    model = {
      event: {
        id: model.event.id,
        start: model.event.start,
        end: model.event.end,
        title: model.event.title
        // other params
      },
      duration: {
        _data: model.duration._data
      }
    };
    this.displayEvent = model;
  }
}



